import * as React from 'react';
import { Drawer, List, StyleRulesCallback, Theme, withStyles } from '@material-ui/core';
import { Motion, spring } from 'react-motion';
import SideMenuItem from './SideMenuItem';
import { MENU } from '../../Constance/MenuConstance';

const styles: StyleRulesCallback = (theme: Theme) => ({
    drawerPaper: {
        position : "fixed",
        width: 200
    },
    toolbar: theme.mixins.toolbar,
    list: {
        marginTop: 100 + "px",
    },
})

interface IProps {
    classes: {
        drawerPaper: string;
        toolbar: string;
        list: string;
    }
    open: boolean;
}

class SideMenu extends React.Component<IProps> {
    constructor(props: IProps) {
        super(props);
    }
    public render() {
        return (
            <Motion
                style={{
                    sub : this.props.open ? 1: 0,
                    width: spring(this.props.open ? 200 : 0)
                }}
            >{
                    style => {
                        return (
                            <Drawer
                                style={{
                                    overflow: "hidden",
                                    width: style.width + "px"
                                }}
                                open={this.props.open}
                                variant="permanent"
                                classes={{
                                    paper: this.props.classes.drawerPaper,
                                }}
                            >
                                <List className={this.props.classes.list}>
                                    {
                                        MENU.map((menu, index) => {
                                            return (
                                                <SideMenuItem
                                                    key={index}
                                                    name={menu.name}
                                                    subMenus={menu.subMenu}
                                                />
                                            )
                                        })
                                    }
                                </List>
                            </Drawer>
                        )
                    }
                }
            </Motion>

        );
    }
}

export default withStyles(styles)(SideMenu)



