import * as React from 'react';
import { MainConsumer } from '../../context/MainContext';
import { StyleRulesCallback, Theme, withStyles } from '@material-ui/core';
import BoardList from './MainView/BoardList';
import BoardView from './MainView/BoardView';
import IntroPage from './IntroPage';
const styles: StyleRulesCallback = (theme: Theme) => ({
    content: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.default,
        padding: theme.spacing.unit * 3,
        minWidth: 0, // So the Typography noWrap works
    },
    toolbar: theme.mixins.toolbar,
    mainContainer: {
        overflow: "auto",
        display: "flex",
        flexBasis: 100 + "%",
        backgroundColor : "#DEDBA7",
    }
})


interface IMainProps {
    classes: {
        content: string;
        toolbar: string;
        mainContainer: string;
    },
    boardCategory?: string;
    subCategory?: string;
    BoardId?: string;
}

interface IState {
    selectedBoardId: string;
}


class Main extends React.Component<IMainProps, IState> {
    constructor(props: IMainProps) {
        super(props);
        this.state = {
            selectedBoardId: this.props.BoardId === undefined ? "" : this.props.BoardId
        }
    }
    public render() {
        return (
            <div className={this.props.classes.mainContainer}>
                <MainConsumer>{
                    value => {
                        return (
                            <main className={this.props.classes.content}>
                                <div className={this.props.classes.toolbar} />
                                {
                                    this.props.boardCategory === null ?
                                        (
                                            <IntroPage />
                                        ) :
                                        this.state.selectedBoardId === "" ?
                                            (
                                                <BoardList
                                                    boardCategory={this.props.boardCategory!}
                                                    subCategory={this.props.subCategory}
                                                    searchKeyword={value.store.searchKeyWord}
                                                />
                                            ) : (
                                                <BoardView selectedBoardId={this.state.selectedBoardId} />
                                            )

                                }
                            </main>
                        );
                    }
                }</MainConsumer>

            </div>
        );
    }
}

export default withStyles(styles)(Main);


