import * as React from 'react';
import { StyleRulesCallback, Theme, withStyles, ListItem, List, ExpansionPanel, ExpansionPanelSummary, Typography, ExpansionPanelDetails } from '@material-ui/core';
import IBoardModel from '../../../Constance/BoardModel';
import axios from 'axios';
import { TransitionMotion, Style, spring, PlainStyle } from 'react-motion';
import { NavLink } from 'react-router-dom';

const style: StyleRulesCallback = (theme: Theme) => ({
    list: {
        width: 100 + "%",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
})

interface IProps {
    classes: {
        list: string;
        heading: string;
    },
    searchKeyword: string;
    boardCategory: string;
    subCategory?: string;
    boardNumber?: string;
}
interface IState {
    boardList: IBoardModel[];
    openPanel: number;
}

class BoardList extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            boardList: [],
            openPanel: -1
        }
        this.handlePanel = this.handlePanel.bind(this);
        this.openSummary = this.openSummary.bind(this);
        this.closeSummary = this.closeSummary.bind(this);
    }
    public componentDidMount() {
        axios.get("https://cors-anywhere.herokuapp.com/http://ec2-54-208-217-206.compute-1.amazonaws.com:8080/board/list", {
            params: {
                category: this.props.boardCategory
            }
        })
            .then((result) => this.setState({
                boardList: result.data
            }))

    }
    public componentWillReceiveProps(nextProps: IProps) {
        if (this.props.boardCategory !== this.props.boardCategory) {
            axios.get("https://cors-anywhere.herokuapp.com/http://ec2-54-208-217-206.compute-1.amazonaws.com:8080/board/list", {
                params: {
                    category: this.props.boardCategory
                }
            }).then((result) => this.setState({
                boardList: result.data
            }))
        }
    }

    public render() {
        const filteredList = this.state.boardList.filter((board, index) => {
            return this.props.subCategory === undefined ? true : board.subCategory === this.props.subCategory
        }).filter((board, index) => {
            return board.title.toLowerCase().indexOf(this.props.searchKeyword) > -1
        })
        return (
            <div style={{
                width: 100 + "%"
            }}>
                <TransitionMotion
                    styles={filteredList.map((list, index) => {
                        return {
                            data: list,
                            key: index + "",
                            style: {
                                opacity: spring(1)
                            }
                        }
                    })}
                    willEnter={this.enterStyle}
                    willLeave={this.leaveStyle}
                >
                    {interpolatedStyles =>
                        <List>
                            {interpolatedStyles.map(config => {
                                const handler = () => this.openSummary(parseInt(config.key, 10))
                                const toAddr = '/blog/' + this.props.boardCategory + '/' + this.props.subCategory + "/" + config.data._id;
                                return (
                                    <ListItem
                                        style={{
                                            width: 100 + "%",
                                            opacity: config.style.opacity
                                        }}
                                        key={config.key}>
                                        <NavLink to={toAddr} style={{
                                            width: 100 + "%",
                                            textDecoration: "none"
                                        }}>
                                            <ExpansionPanel
                                                expanded={this.state.openPanel === parseInt(config.key, 10) ? true : false}
                                                className={this.props.classes.list}
                                                onMouseEnter={handler}
                                                onMouseLeave={this.closeSummary}
                                            >
                                                <ExpansionPanelSummary>
                                                    <Typography className={this.props.classes.heading} variant="body1">{config.data.title}</Typography>
                                                </ExpansionPanelSummary>
                                                <ExpansionPanelDetails>
                                                    <Typography variant="body2">{config.data.summary}</Typography>
                                                </ExpansionPanelDetails>
                                            </ExpansionPanel>
                                        </NavLink>
                                    </ListItem>
                                );
                            })}
                        </List>
                    }
                </TransitionMotion>
            </div>
        );
    }
    private handlePanel(num: number) {
        if (this.state.openPanel === num) {
            this.setState({ openPanel: -1 });
        } else {
            this.setState({ openPanel: num });

        }
    }
    private enterStyle(): PlainStyle {
        return {
            opacity: 0
        }
    }
    private leaveStyle(): Style {
        return {
            opacity: spring(0)
        }
    }
    private openSummary(num: number) {
        this.setState({
            openPanel: num
        });
    }
    private closeSummary() {
        this.setState({
            openPanel: -1
        });
    }
}

export default withStyles(style)(BoardList);


