import * as React from 'react';
import { StyleRulesCallback, Theme, withStyles, List, ListItem, ListItemText, Collapse } from '@material-ui/core';
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import { NavLink } from 'react-router-dom';
import { RouterConsumer } from '../../context/RouterContext';

const styles: StyleRulesCallback = (theme: Theme) => ({
    link: {
        textDecoration: "none"
    }
})
// 여기서 color 는 List의 버튼 클릭시 생기는 색깔임
interface IProps {
    classes: {
        list: string;
        listItem: string;
        link: string;
    }
    name: string;
    subMenus: string[];
}
interface IState {
    open: boolean;
}

class SideMenuItems extends React.Component<IProps, IState>{
    constructor(props: IProps) {
        super(props);
        this.state = {
            open: false
        }
        this.handler = this.handler.bind(this);
    }
    public render() {
        return (
            <React.Fragment>
                <ListItem button={true} className={this.props.classes.listItem} onClick={this.handler}>
                    <ListItemText primary={this.props.name} />
                    {this.state.open ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Collapse in={this.state.open}>
                    <List>
                        {
                            this.props.subMenus.map((subMenu, index) => {
                                const addr = "/blog/" + this.props.name + "/" + subMenu;
                                return (
                                    <RouterConsumer key={index}>
                                        {
                                            value => {
                                                const handler =()=> value.handleUrl(addr);
                                                return (
                                                    <NavLink
                                                        to={addr}
                                                        className={this.props.classes.link}
                                                        replace={value.store.nowUrl === addr ? true : false}
                                                        onClick={handler}
                                                    >
                                                        <ListItem button={true} className={this.props.classes.listItem}>
                                                            <ListItemText inset={true} primary={subMenu} />
                                                        </ListItem>
                                                    </NavLink>
                                                )
                                            }
                                        }
                                    </RouterConsumer>
                                );
                            })
                        }
                    </List>
                </Collapse>
            </React.Fragment>
        );
    }
    private handler() {
        this.setState({
            open: !this.state.open
        })
    }
}

export default withStyles(styles)(SideMenuItems);


