import * as React from 'react';
import IBoardModel from '../../../Constance/BoardModel';
import { Typography, Divider, StyleRulesCallback, Theme, withStyles } from '@material-ui/core';
import axios from 'axios';
const style: StyleRulesCallback = (theme: Theme) => ({
    title: {
        width: 100 + "%",
        textAlign: "left",
        padding: 20 + "px",
        fontSize: 40 + "px",
    }, boardContents: {
    }
})
interface IProps {
    classes: {
        title: string,
        boardContents: string;
    }
    selectedBoardId: string;
}
interface IState {
    board: IBoardModel;
    contents: string;
}
class BoardView extends React.Component<IProps, IState>{
    constructor(props: IProps) {
        super(props);
        this.state = {
            board: {
                contentsAddress: "",
                category: "",
                subCategory: "",
                updateDate: new Date(),
                regDate: new Date(),
                summary: "",
                title: "",
                _id: -1
            },
            contents: ""
        }
    }
    public componentWillReceiveProps(nextProps: IProps) {
        axios.get("https://cors-anywhere.herokuapp.com/http://ec2-54-208-217-206.compute-1.amazonaws.com:8080/board/getBoard", {
            params: {
                _id: this.props.selectedBoardId
            }
        })
            .then((result) => {
                this.setState({ board: result.data })
                axios.post("https://cors-anywhere.herokuapp.com/http://ec2-54-208-217-206.compute-1.amazonaws.com:8080/board/content", {
                    addr: this.state.board.contentsAddress
                })
                    .then((result2) => this.setState({ contents: result2.data }))
            })
    }
    public componentDidMount() {
        axios.get("https://cors-anywhere.herokuapp.com/http://ec2-54-208-217-206.compute-1.amazonaws.com:8080/board/getBoard", {
            params: {
                _id: this.props.selectedBoardId
            }
        })
            .then((result) => {
                this.setState({ board: result.data })
                axios.post("https://cors-anywhere.herokuapp.com/http://ec2-54-208-217-206.compute-1.amazonaws.com:8080/board/content", {
                    addr: this.state.board.contentsAddress
                })
                    .then((result2) => this.setState({ contents: result2.data }))
            })
    }
    public render() {
        return (
            <div className={this.props.classes.boardContents}>
                <Typography
                    className={this.props.classes.title}
                    variant="title"
                >
                    {this.state.board.title}
                </Typography>
                <Divider />
                <div
                    dangerouslySetInnerHTML={{ __html: this.state.contents }}
                />
            </div>
        );
    }
}

export default withStyles(style)(BoardView);


