import * as React from 'react';

interface IRouterStore {
    nowUrl: string;
}

interface IRouterContextValue {
    store: IRouterStore
    handleUrl(keyword: string): void;
}

const RouterContext = React.createContext<IRouterContextValue>({
    store: {
        nowUrl: "/blog"
    },
    handleUrl: (keyword: string) => { "" }
});
export const RouterConsumer = RouterContext.Consumer;
class RouterProvider extends React.Component<{}, IRouterStore> {
    constructor(props: {}) {
        super(props);
        this.state = {
            nowUrl: "/blog"
        }
        this.handleUrl = this.handleUrl.bind(this);
    }

    public render() {
        return (
            <RouterContext.Provider value={{
                store: this.state,
                handleUrl: this.handleUrl
            }}>
                {this.props.children}
            </RouterContext.Provider>
        );
    }
    private handleUrl(url: string) {
        this.setState({
            nowUrl: url
        })
    }
}
export { RouterProvider };


