import * as React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Search from '@material-ui/icons/Search';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import InputAdornment from '@material-ui/core/InputAdornment';
import { StyleRulesCallback, Theme, withStyles } from '@material-ui/core';
import { NavLink } from 'react-router-dom';
import { MainConsumer } from '../../context/MainContext';

const styles: StyleRulesCallback = (theme: Theme) => ({
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        position:"fixed"
    },
    title: {
        marginLeft: 25,
    }
})

interface IProps {
    classes: {
        appBar: string;
        title: string;
    }
}

class TopBar extends React.Component<IProps>{
    constructor(props: IProps) {
        super(props);
    }
    public render() {
        return (
            <AppBar position="absolute" className={this.props.classes.appBar}>
                <Toolbar>
                    <NavLink to="/blog/" style={{color:"inherit", textDecoration:"none" }}>
                        <Typography variant="title" color="inherit" noWrap={true} className={this.props.classes.title}>
                            CrystalValley's Blog
                     </Typography>
                    </NavLink>
                    <MainConsumer>{
                        value => {
                            const handler = (e: React.ChangeEvent<HTMLInputElement>) => { value.handleKeyWord(e.currentTarget.value) }
                            return (
                                <TextField
                                    style={{
                                        position: "absolute",
                                        left: 85 + "%",
                                        width: 15 + "%"
                                    }}
                                    onChange={handler}
                                    id="input-with-icon-textfield"
                                    label="Search Keyword"
                                    InputProps={{
                                        endAdornment: (
                                            <InputAdornment position="end">
                                                <Search />
                                            </InputAdornment>
                                        ),
                                    }}
                                />
                            )
                        }
                    }
                    </MainConsumer>
                </Toolbar>
            </AppBar>
        );
    }
}

export default withStyles(styles)(TopBar);


