import * as React from 'react';
import Main from './Main';
import { Route, Switch} from 'react-router-dom';

export default class MainRouter extends React.Component {
    public render() {
        const handler = ({ match }) => (<Main
            boardCategory={match.params.boardCategory}
            subCategory={match.params.subCategory}
            BoardId={match.params.BoardId}
        />)
        return (
                <Switch>
                    <Route exact={true} path="/blog/" component={handler} />
                    <Route exact={true} path="/blog/:boardCategory/:subCategory?/:BoardId?" component={handler} />
                </Switch>
        );
    }
}


